import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { TabPage } from './tabpage.page';

const routes: Routes = [
  {
    path: '',
    component: TabPage,
    children: [{
      path: 'dashboard',
      loadChildren: () => import('./dashboard/dashboard.module').then(m => m.DashboardPageModule)
    },
    {
      path: 'routes',
      loadChildren: () => import('./routes/routes.module').then(m => m.RoutesPageModule)
    },
    {
      path: 'completed-routes',
      loadChildren: () => import('./completed-routes/completed-routes.module').then(m => m.CompletedRoutesPageModule)
    },
    {
      path: 'stop-list',
      loadChildren: () => import('./stop-list/stop-list.module').then(m => m.StopListPageModule)
    },
    {
      path: 'map',
      loadChildren: () => import('./map/map.module').then(m => m.MapPageModule)
    },
    {
      path: '',
      redirectTo: 'routes',
      pathMatch: 'full'
    }]
  }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TabPageRoutingModule { }
